// axiosget
export function fetchGet (url, params = {}) {
  return new Promise((resolve, reject) => {
    axios.get(url, {
      params: params
    })
      .then(response => {
        resolve(response.data)
      })
      .catch((error) => {
        reject(error)
      })
  })
}

// axiospost
export function fetchPost (url, data = {}) {
  return new Promise((resolve, reject) => {
    axios.post(url, data)
      .then(response => {
        resolve(response.data)
      })
      .catch((error) => {
        reject(error)
      })
  })
}

export default {
  getNewsLists (url, params) {
    return fetchGet(url, params)
  },
  getBrief (url, params) {
    return fetchGet(url, params)
  }
}



